// Switch element
.form-switch {
  @apply relative select-none;
  width: 60px;

  label {
    @apply block overflow-hidden cursor-pointer h-8 rounded-full;

    > span:first-child {
      @apply absolute block rounded-full;
      width: 28px;
      height: 28px;
      top: 2px;
      left: 2px;
      right: 50%;
      transition: all 0.15s ease-out;
    }
  }

  input[type="checkbox"] {
    &:checked {
      + label {
        @apply bg-purple-600;

        > span:first-child {
          left: 30px;
        }
      }
    }
  }
}
